.body{
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    background-color: whitesmoke;
}


.main{
    width:max-content;
    background-color:lightsalmon;
    position: relative;
    margin: auto;
    width: 50em;
    height: 100%;
    padding-top:16px;
    box-sizing: border-box;
    box-shadow: 0 4px 8px 0 rgb(0, 0, 0), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}


.todolisty{
background-color:lightgrey;
border: 2px solid rgb(20, 17, 17);
width: fit-content;
margin: auto;
margin-top: 10px;
box-shadow: 0 4px 8px 0 rgb(0, 0, 0), 0px 6px 20px 6px rgba(0, 0, 0, 0.19); 
}

.todolist{
    width: 60%;
    background-color: lightgray;
    text-align: center;
    margin: auto;
    margin-top: 16px;
    box-shadow: 0 4px 8px 0 rgb(0, 0, 0), 0px 6px 20px 0 rgba(0, 0, 0, 0.19);
}


.clist{
    /* background-color:lightgrey;
    border: 2px solid rgb(20, 17, 17); */
    width: 90%;
    height: 45%;
    margin: auto;
    margin-top: 10px;
    /* box-shadow: 0 4px 8px 0 rgb(0, 0, 0), 0px 6px 20px 6px rgba(0, 0, 0, 0.19);   */
    padding: 16px;
    overflow-y: scroll;
    

}






#dtodo{
    width:96%;
    border: 1px solid black;
    margin-bottom: 16px;
    padding: 0.5em;
    padding-top: 0;
    padding-top:0em;
    background-color:rgb(211, 211, 211);
   
    box-shadow: 0 4px 8px 0 rgb(0, 0, 0), 0px 6px 20px 0 rgba(0, 0, 0, 0.19);
    position: relative;
   
}

#dtodo2{
  display: inline-block;
  width: 62%;

  text-align: center;
  box-sizing: border-box;
}
#dtodo3{
    display:inline-block;
    width: 19%;
    height: fit-content;
    box-sizing: border-box;
    text-align: center;
}

#dtodo4{
    display:inline-block;
    width: 19%;
    box-sizing: border-box;
    text-align: center;
}

#delimg{
    width: 70px;
  margin: 16px;
      }

.todoname{
    
   background-color: rgb(253, 253, 253);
   text-align: center;
   box-shadow: 0 4px 8px 0 rgb(0, 0, 0), 0px 6px 20px 0 rgba(0, 0, 0, 0.19);
   
   margin: 5px;
   word-wrap: break-word;
   width: 100%; 



}

p{

word-wrap: break-word;
width: 100%;
}

.editbox{
    background-color:lightgrey;
    border: 2px solid rgb(20, 17, 17);
    width: fit-content;
    height: fit-content;
    box-shadow: 0 4px 8px 0 rgb(0, 0, 0), 0px 6px 20px 6px rgba(0, 0, 0, 0.19); 
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin: auto;
    outline: 100em solid rgba(0, 0, 0, 0.873);
    }


    @media only screen and (max-width: 1000px) {
        .main{
            width: 100%;
            box-sizing: border-box;
        }
        .todolisty{
            width: 90%;
            text-align: center;
        }
        .todolisty input{
            display: block;
            width: 50%;
            margin: auto;
            
        }

        .todolisty textarea{
            width: 100%;
        }

        .editbox{
            width: 90%;
            text-align: center;

        }

        .editbox input{
            width: 50%;
            display: block;
            margin: auto;
        }

        .editbox textarea{
            width: 100%;
        }

        #delimg{
            width: 3em;
        }
    }
    

